import React from 'react';
import { Container, Col, Row, Button } from "react-bootstrap";
import ReactPlayer from 'react-player'
import NewsItems from './NewsItemsV1'
import './basis.css';

const styles = {
    homeContainer: {
        height: 1356,
        backgroundImage: `url(${"/testreact/images/2020-09-10 Eerste oefensessie overzicht.jfif"})`,
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        backgroundSize: 'cover'
    },

    content: {
      height: '100%',
      width: '100%',
      backgroundColor: 'rgba(250, 250, 250, 0.8)',
    }
};

class Home extends React.Component {
  
  render () {
    return (
      <div style={styles.homeContainer}>
        <Container style={styles.content} fluid="md">
            <Row xs={1} md={1}  className="row">
                <h4>Muzikale Verbinding in Zilverkamp Huissen </h4>
            </Row>
        
            <Row xs={12} md={6} lg={9} className="row">
              <Col xs={12} md={10} lg={9} className="col">      
               <ReactPlayer width="100%" controls= "true"  playing={true}  url='https://youtu.be/t3XmOETQ3ss' />
              </Col>
              <Col xs={12} md={2} lg={3} className="col">         
                <img width= "100%" src={process.env.PUBLIC_URL + '/images/2020-09-10 Hans on percussion.jfif'} alt="2020-09-10 Hans on percussion" />
                Interesse? mail Hans jhjmvos@gmail.com  
              </Col>
            </Row>    
            <Row xs={1} md={1}  className="row">
              <NewsItems pageFilter="home" newButton={false}/>
            </Row>
        </Container>
      </div>
   )
  }
}

export default Home  